//
// The basic page structure:
//
// body[BODY CLASSES]
//   a.skip-link
//   #site-navigation.main-navigation.js-menu-area
//   #site-search.main-search.js-menu-area
//   #page.site
//       [ROW] #masthead.site-header
//           [COLUMN 12] .site-branding
//               h1.site-title
//               h2.site-description
//       #content.site-content
//           [ROW] #primary.content-area
//               main#main.site-main
//                   header.above-single-header
//                       [COLUMN 12] .issue-header
//                       [COLUMN 12] .category-header
//  ############################################################################
//  #                section.issue-section.category-section
//  #                    [COLUMN 12/10] .category-header.issue-section-header
//  #                        h2.category-title.issue-section-title
//  #                    [COLUMN 12/10] .category-content.index-content.issue-section-content
//  #                        .category-posts.index-posts.issue-section-posts
//  #                            article.entry[POST CLASSES]
//  #                                [...]
//  ############################################################################
//                    article.entry[POST CLASSES]
//                       [COLUMN x/10/8] .entry-image
//                       [COLUMN 12/10/8] .entry-header
//                           a.entry-title-link
//                               h1.entry-title
//                               h2.entry-subtitle
//                           .entry-meta
//                           	.byline
//                       [COLUMN 12/10/8/6] .entry-content
//                           [CONTENT]
//                       .footer.entry-footer
//                           [COLUMN 12/10/8/6] .entry-author
//                           [COLUMN 12/10/8/6] .posted-on
//                           [COLUMN 12/10/8/6] .tags-links
//                   #comments.comments-footer
//                       [COLUMN 12/10] .comments-area
//                           h2.comments-title
//                           [NESTED-ROW] #comment-nav-above.comment-navigation
//                           	[COLUMN 12/5] .nav-previous
//                           	[COLUMN 12/5] .nav-next
//                           [NESTED-ROW] ol.comment-list
//                           	[COLUMN 12/10/5] li.comment
//                           		article.comment-body
//                           			footer.comment-meta
//                           				.comment-author.vcard
//                           			.comment-content
//                           			.reply
//                           				.comment-reply-link
//                           [NESTED-ROW] #comment-nav-below.comment-navigation
//                           	[COLUMN 12/5] .nav-previous
//                           	[COLUMN 12/5] .nav-next
//                           #respond.comment-respond
//           [ROW] #secondary.widget-area.footer-widget-area
//               [COLUMN 12/6/4] [WIDGETS]
//           [ROW] #tertiary.footer.site-footer
//               [COLUMN 12] .site-footer-content

.site-header,
.content-area,
.footer-widget-area,
.site-footer {
	@include row;
}

.comment-navigation,
.comment-list {
	@include nested-row;
}


/* Every column is full-width for mobiles. */
.site-branding,
.category-header,
.index-content,
.single-entry .entry-header,
.single-entry .entry-content,
.single-entry .entry-author,
.single-entry .posted-on,
.single-entry .tags-links,
.nav-previous,
.nav-next,
.comments-area,
.comment,
.site-footer-content {
	@include column(12);
}

@include media( "main-spacing" ) {

	/*
	 * Leave a column free on either side of most content, for the Menu and
	 * Search toggles.
	 */
	.category-header,
	.index-content,
	.single-entry .entry-header,
	.single-entry .entry-content,
	.single-entry .entry-author,
	.single-entry .posted-on,
	.single-entry .tags-links,
	.comments-area {
		@include column(10);
		@include push;
		@include pull;
	}

	/* Nested single column. */
	.comment {
		@include column(10, $padding, 10);
	}


	/* Nested side-by-side columns. */
	.nav-previous,
	.nav-next {
		@include column(5, $padding, 10);
	}
}

@include media( "main-spacing-1" ) {

	/* Start limiting featured image size. */
	.single-entry .entry-image {
		@include column(10);
		@include push;
		@include pull;
	}

	/* Narrower content column than everything else, for better line length. */
	.single-entry .entry-content,
	.single-entry .entry-author,
	.single-entry .posted-on,
	.single-entry .tags-links {
		@include column(8);
		@include push(2);
		@include pull(2);
	}

	/* More spacing between side-by-side columns. */
	.nav-previous,
	.nav-next {
		@include column(4, $padding, 10);
	}

	.nav-previous {
		@include pull(1, 10);
	}
	.nav-next {
		@include push(1, 10);
	}

}

@include media( "main-spacing-2" ) {

	/* Narrower heading area. */
	.single-entry .entry-image,
	.single-entry .entry-header {
		@include column(8);
		@include push(2);
		@include pull(2);
	}

	/* Even narrower content column, to maintain sensible line length. */
	.single-entry .entry-content,
	.single-entry .entry-author,
	.single-entry .posted-on,
	.single-entry .tags-links {
		@include column(6);
		@include push(3);
		@include pull(3);
	}

	/* Side-by-side comments. */
	.comment {
		@include column(5, $padding, 10);
	}

	/* Left-aligned single post layout. */
	.template-fixed-left,
	.template-scrolling-left {

		.entry-image {
			@include column(4);
			margin-left: 0;
			margin-right: 0;
		}

		.entry-header,
		.entry-content {
			@include column(6);
			@include pull;
			@include push(5);
		}

	}

	/* Right-aligned single post layout. */
	.template-fixed-right,
	.template-scrolling-right {

		.entry-image {
			@include column(4);
			margin-right: 0;
			margin-left: 0;
		}

		.entry-header,
		.entry-content {
			@include column(6);
			@include pull(5);
			@include push;
		}

	}

}

@include media( "main-spacing-3" ) {

	.template-fixed-left,
	.template-scrolling-left {

		.entry-header,
		.entry-content {
			@include column(5);
			@include pull;
			@include push(6);
		}

	}

	.template-fixed-right,
	.template-scrolling-right {

		.entry-header,
		.entry-content {
			@include column(5);
			@include pull(6);
			@include push;
		}

	}

}
