/* 5.2 Menus
--------------------------------------------------------------*/
/* Animation for moving the page 'out of the way' of the sidebars. */
.site {
  transition: 0.1s transform ease-in; }

/* Menu sidebar. */
.main-navigation {
  clear: both;
  display: block;
  float: left;
  width: 100%; }
  .main-navigation ul {
    font-size: 16px;
    font-size: 0.88889rem;
    list-style: none;
    margin: 0;
    padding-left: 0;
    font-family: "Lato", "Proxima Nova", Corbel, Helvetica, sans-serif;
    text-transform: uppercase;
    letter-spacing: 0.05em; }

/*
 * 1. Even though these are overlapped by the main content area, we still have
 *    to hide them until they're displayed so they don't peek out above or
 *    below the main content when the scrolling is rubber-banding.
 */
.main-navigation-wrapper, .main-search-wrapper {
  display: none;
  /* 1 */
  width: 15.55556rem;
  height: 100%;
  position: fixed;
  overflow-y: scroll;
  top: 0;
  color: #fff;
  /* Make sure links are visible. */ }
  .main-navigation-wrapper a, .main-search-wrapper a {
    font-weight: bold;
    color: #69bcc9;
    border-bottom: 0; }
    .main-navigation-wrapper a:hover, .main-navigation-wrapper a:focus, .main-navigation-wrapper a:active, .main-search-wrapper a:hover, .main-search-wrapper a:focus, .main-search-wrapper a:active {
      color: #a1d5dd; }

.main-navigation-wrapper {
  left: 0; }
  .main-navigation-wrapper a {
    display: block;
    text-decoration: none;
    color: #fff; }

.main-search-wrapper {
  right: 0;
  /* Right-aligned elements. */ }
  .main-search-wrapper .widget {
    border-left: 0;
    border-right-width: 0.77778rem;
    border-right-style: solid; }

/*
 * Sidebar toggle buttons.
 * 1. Overlay the main .site container.
 */
.menu-toggle, .search-toggle {
  font-size: 18px;
  font-size: 1rem;
  width: 224px;
  width: 12.44444rem;
  height: 84px;
  height: 4.66667rem;
  padding-top: 28px;
  padding-top: 1.55556rem;
  line-height: 84px;
  line-height: 4.66667rem;
  position: fixed;
  top: 0;
  z-index: 10;
  /* 1 */
  display: block;
  border: 0;
  border-radius: 0;
  box-shadow: none;
  text-shadow: none;
  background: transparent;
  font-weight: bold;
  font-family: "Lato", "Proxima Nova", Corbel, Helvetica, sans-serif;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  color: #9696a0;
  /* Same transition duration as links. */ }
  .menu-toggle, .menu-toggle:hover, .search-toggle, .search-toggle:hover {
    transition: 0.2s color ease-in; }
  .menu-toggle:hover, .menu-toggle:focus, .menu-toggle:active, .search-toggle:hover, .search-toggle:focus, .search-toggle:active {
    color: #171717; }
  .menu-toggle:hover, .menu-toggle:focus, .menu-toggle:active, .search-toggle:hover, .search-toggle:focus, .search-toggle:active {
    box-shadow: none;
    text-shadow: none; }

.menu-toggle-outer, .search-toggle-outer {
  line-height: 56px;
  line-height: 3.11111rem;
  display: inline-block;
  padding: 0 0.5em;
  background: #fff; }

.menu-toggle {
  left: 84px;
  left: 4.66667rem;
  transform: rotate(90deg);
  transform-origin: left top 0;
  transition: 0.1s left ease-in, 0 opacity linear 0.1s;
  /* Delay on show, to wait for main page to slide back over. */ }

.search-toggle {
  right: 84px;
  right: 4.66667rem;
  transform: rotate(-90deg);
  transform-origin: right top 0;
  transition: 0.1s right ease-in, 0 opacity linear 0.1s;
  /* Delay on show, to wait for main page to slide back over. */ }

/* Menu area is open. */
.menu-left-toggled {
  /* Shift the page over. */
  /* Show the navigation elements. */
  /* Hide the Search toggle button, it's unusable until we close this menu. */ }
  .menu-left-toggled .site {
    position: relative;
    transform: translate3d(280px, 0, 0);
    transform: translate3d(15.55556rem, 0, 0); }
  .menu-left-toggled .menu-toggle {
